@import url(https://fonts.googleapis.com/css?family=Poiret+One&display=swap);
html,body{height:100%;background:-webkit-gradient(linear, left top, right top, from(#0f2027), color-stop(50%, #203a43), to(#2c5364));background:linear-gradient(90deg, #0f2027 0%, #203a43 50%, #2c5364 100%)}.container{padding-top:20px;font-family:'Poiret One', cursive}.calculator{width:350px;height:550px;margin:0 auto;border-radius:3px;background-color:rgba(0,0,0,0.7);box-shadow:black 0px 5px 5px;color:white}.calculator__path,.calculator__number{letter-spacing:3px;padding:5px;text-align:right;overflow-x:auto;overflow-y:hidden}.calculator__path::-webkit-scrollbar,.calculator__number::-webkit-scrollbar{width:24px;height:2px}.calculator__path::-webkit-scrollbar-thumb,.calculator__number::-webkit-scrollbar-thumb{background-color:#817e7e;border-radius:3px}.calculator__path{height:6%;line-height:100%;font-size:30px;font-weight:200}.calculator__number{height:12%;font-size:50px;text-align:right;border-bottom:1px solid #bbb;box-sizing:border-box}.calculator__panel{display:flex;flex-direction:column;height:82%;font-size:50px}.calculator__panel-row{display:flex;flex-direction:row;height:20%;align-items:center}.calculator__panel-button{flex:1 1;text-align:center;cursor:pointer}.calculator__panel-button:last-child{color:#fd3838}

